  watch: {
    $fetchState: {
      async handler(state) {
        if (state.pending === true || state.error !== null) return
        await this.$nextTick()
        this.$applyMediumZoom()
        Prism.highlightAll()

        document.querySelectorAll(".nuxt-content-highlight").forEach((el) => {
